<template>
  <div>
    <div class="row">
      <div class="col">
        <h3 class="fw-bold">Edit Page</h3>
      </div>
    </div>
    <template>
      <div class="card">
        <div class="card-body my-4">
          <v-form ref="form" v-model="valid" lazy-validation>

            <v-autocomplete label="Column" v-model="pageData.column_id" :items="columns" item-text="columns_name" item-value="id" autocomplete="on" ></v-autocomplete>

            <v-text-field label="Page Name" v-model="pageData.page_name"></v-text-field>

            <v-switch v-model="pageData.status" label="Active" ></v-switch>

            <!-- <v-switch v-model="pageData.page_status" label="Static" @click="toggleTextField" value="1"></v-switch> -->

            <!-- <div v-if="pageData.page_status == '1'"> -->
              <!-- <v-text-field label="Page Link" v-model="pageData.page_link" v-if="showTextField"></v-text-field> -->
            <!-- </div> -->
            <!-- <div v-if="pageData.page_status == '0'"> -->
              <v-text-field label="Page Link" v-model="pageData.page_link"></v-text-field>
            <!-- </div> -->

            <v-btn color="success" class="mr-4" @click="updatePageDetails">
              UPDATE
            </v-btn>
            <router-link to="/admin/pages">
              <v-btn color="error" class="mr-4" @click="reset">
                CANCEL
              </v-btn>
            </router-link>
          </v-form>
        </div>
      </div>
      <v-snackbar v-model="snackbar" :timeout="timeout">{{ text }}
        <template v-slot:action="{attrs}">
          <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
        </template>
      </v-snackbar>
    </template>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  props: {
    page_id: Number,
  },
  components: {},
  data (){
    return{
      switch1: false,
      switch2: false,
      columns:[],
      pageData:{},
      snackbar: false,
      switchStatus: '',
      text: '',
      timeout: '',
      formData:{
        column:'',
        page_name:'',
        status:'',
        page_status:''
      },
      
    }
  },

  created(){
    this.getColumns();
    this.getPageDetail();
    // this.toggleTextField();
  },
  methods: {

    // toggleTextField() {
    //   console.log(this.switchStatus)
    //   if( this.switchStatus == 0 ){
    //     this.showTextField = false;
    //   }else{
    //     this.showTextField = true;
    //   }
    // },

    // validate() {
    //   this.$refs.form.validate();
    // },

    getColumns(){
      axios.get("get/columns").then( (resp) => {
        this.columns = resp.data.columns
      })
    },

    getPageDetail(){
      axios.get("get/page/detail?id="+this.page_id).then( (resp) => {
        this.pageData = resp.data.page
        // if( this.pageData.page_status == '0' ){
        //   this.switchStatus = 0;
        // }else{
        //   this.switchStatus = 1;
        // }
      })
    },
    updatePageDetails(){
      
      var data = {
        page_id: this.page_id,
        column_id : this.pageData.column_id,
        page_name : this.pageData.page_name,
        page_link : this.pageData.page_link,
        status : this.pageData.status,
        page_status : this.pageData.page_status
      };

      axios.post("update/page",data).then( (resp)=> {
        this.snackbar = true;
        this.text = resp.data.message;
        this.timeout = 3000;
        this.getPageDetail();
      })
    }
  },
};
</script>
  